.container{
    color: white;
}

.option{
    transition: 0.3s;
    border: 3px solid white;
    border-radius: 15px;
    height: 75px;
    line-height: 75px;
    background-color: black;
}

.option:hover{
    color: black;
    background-color: white;
    border: 3px solid black;
}

h3{
    color: white;
    font-size: 30px;
}

.score{
    font-size: 25px;
}

.list{
    font-size: 22px;
}

.answers{
    list-style-type: none;
}

.answers li{
    margin-top: 5px;
}

.answers li span{
    color: lightgreen
}

.homepage{
    display: block;
    width: 250px;
    margin: auto;
}